<template>
  <aside>
    <h1>{{$store.getters.get_organization}}</h1>
    <battons 
      v-on:call_event="call_client"
      v-on:success_event="confirm_client"
      v-on:dalay_event="delay_client"
      v-on:calldelay_event ="call_delay_client"
      v-on:change_service ="change_service_client"
    ></battons>
    <div class="ticket" v-if="$store.getters.get_current_ticket?.num">
      <span>{{$store.getters.get_current_ticket?.prefix}}{{$store.getters.get_current_ticket?.num}}</span>
      <span>{{$store.getters.get_current_ticket.service?.name}}</span>
    </div>
  </aside>
  <main>
    <h1>{{$store.getters.get_location}}</h1>
    <status></status>
  </main>
</template>

<script>
import axios from 'axios'
import battons from './components/buttons.vue'
import status from './components/status.vue'
export default {
  name: 'App',
  components: {
    battons,
    status
  },
  created() {
    this.init();
    setTimeout(() => {
      this.$socket.emit('get_ticket', {})
    }, 2000)
  },
  methods: {
    init() {
      axios.get(this.$store.getters.get_url[0]).then(resp => {
        this.$store.commit('set_location', resp.data.location);
        this.$store.commit('set_organization', resp.data.organization);
        this.$store.commit('set_operator', resp.data.operator);
        this.$store.commit('set_services', resp.data.services)
      })
    },
    call_client() {
      this.$socket.emit('call_client', {room: this.$store.getters.get_room_id});
    },
    confirm_client() {
      if (confirm("выполнить?")) {
        this.$socket.emit('confirm_client', {room: this.$store.getters.get_room_id});
      }
      
    },
    delay_client() {
      if (confirm("выполнить?")) {
        this.$socket.emit('delay_client', {room: this.$store.getters.get_room_id});
      }
    },
    call_delay_client() {
      this.$socket.emit('call_delay_client', {room: this.$store.getters.get_room_id});
    },
    change_service_client(service_id) {
      if (confirm("выполнить?")) {
        this.$socket.emit('change_service_client', {room: this.$store.getters.get_room_id, service_id: service_id});
      }
    }
  },
  sockets: {
    settings(data) {
      this.$store.commit('set_room_id', data.room_id);
    },
    state(data) {
      this.$store.commit('set_state', data);
    },
    ticket(data) {
      this.$store.commit('set_current_ticket', data?.ticket);
    }
  }
}
</script>

