<template>
    <div class="wrapp">
        <div class="button" v-on:click="call_event">вызвать</div>
        <div class="button" v-on:click="success_event">завершить</div>
        <div class="button" v-on:click="dalay_event">отложить</div>
        <div class="button" v-on:click="calldelay_event">вызвать отложенного</div>
        <div class="union">
            <select class="button" v-model="service_id">
                <option v-for="item in $store.getters.get_services" :key="item" :value="item.id">{{item.name}}</option>
            </select>
            <div class="button" v-on:click="change_service">изменить услугу</div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            service_id: 0,
        }
    },
    methods: {
        call_event() {
            this.$emit('call_event');
        },
        success_event() {
            this.$emit('success_event');
        },
        dalay_event() {
            this.$emit('dalay_event');
        },
        calldelay_event() {
            this.$emit('calldelay_event');
        },
        change_service() {
            this.$emit('change_service', this.service_id);
        }
    } 
}
</script>

<style lang="scss" scoped>
.button {
    padding: 1em;
    color: rgb(138, 64, 64);
    text-align: center;
    background-color: rgb(255, 226, 188);
    width: 80%;
    margin: 0 auto;
    border-radius: 5px;
    margin-top: 0.5em;
}

.button:hover {
    transition: 0.3s;
    background-color: rgb(93, 66, 168);
    color: white;
}

.button:active {
    transition: 0.3s;
    background-color: tomato;
    color: white;
}

.union {
    background-color: rgb(166, 135, 250);
    padding: 1em;
    margin: 1em;
    border-radius: 10px;
}
</style>