import { createStore } from 'vuex'

const store = createStore({
    state() {
        return {
            room_id: "",
            location: "location",
            organization: "organiztion",
            urls: [
                'http://127.0.0.1:5000/api/v1/operator_settings'
            ],
            operator: {},
            last_ticket: {
                num: 0,
                prefix: "prefix",
                time: "",
                service: {
                    id: 0,
                    name: "service"
                }
            },
            current_ticket: {
                num: 0,
                prefix: "prefix",
                time: "",
                service: {
                    id: 0,
                    name: "service"
                }
            },
            stat: {},
            services: []
        }
    },
    mutations: {
        set_room_id(state, id) {
            state.room_id = id;
        },
        set_location(state, loction) {
            state.location = loction
        },
        set_organization(state, organization) {
            state.organization = organization;
        },
        set_last_ticket(state, ticket) {
            state.last_ticket = ticket
        },
        set_current_ticket(state, ticket) {
            state.current_ticket = ticket
        },
        set_state(state, stat) {
            state.stat = stat;
        },
        set_operator(state, operator) {
            state.operator = operator;
        },
        set_services(state, services) {
            state.services = services
        }
    },
    getters: {
        get_room_id(state) {
            return state.room_id
        },
        get_url(state) {
            return state.urls
        },
        get_location(state) {
            return state.location
        },
        get_organization(state) {
            return state.organization
        },
        get_last_ticket(state) {
            return state.last_ticket
        },
        get_current_ticket(state) {
            return state.current_ticket
        },
        get_state(state) {
            return state.stat
        },
        get_operator(state) {
            return state.operator
        },
        get_services(state) {
            return state.services
        }
    }
})

export default store